[data-tabs='parent'] {
  overflow: hidden;

  &.no-transition {
    > [data-tabs='control'],
    > [data-tabs='content'],
    > [data-tabs='element'] {
      transition: none;
    }
  }

  &.no-transition-global {
    [data-tabs='control'],
    [data-tabs='content'],
    [data-tabs='element'] {
      transition: none;
    }
  }
}

[data-tabs='parent'] [data-tabs='content'] {
  position: relative;
}

[data-tabs='element'] {
  visibility: hidden;
  display: none;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs='element'].for-load {
  position: static;
  visibility: visible;
  opacity: 1;

  transition: none;
}

[data-tabs='element'].is-active {
  z-index: 1;
  display: block;
  visibility: visible;
  opacity: 1;
}
